import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
// import { Redirect } from "react-router-dom";

import ProjectUpdateForm from "./updateForm";
import ProjectHistoryUpdateForm from "./updateHistoryForm";

import notification from "../../../../components/notification";
import { Spin, Tabs } from "antd";


import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/companyMaster/project/actions";
import { Box } from "../../../App/app.style";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/setup/projects";

class UpdateProject extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editProject } = this.props;
    const { id } = this.props.match.params;
    editProject(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.project.error) {
      notification("error", this.props.project.error);
      this.props.clearError();
    }

    if (this.props.project.success) {
      notification("success", this.props.project.success);
      this.props.clearSuccess();
      // this.props.history.replace("/dashboard/setup/projects")
      // const { editProject } = this.props;
      // const { id } = this.props.match.params;
      // editProject(id);
    }
  }

  handleSubmit = value => {
    const { updateProject } = this.props;
    const { id } = this.props.match.params;
    // Uppercasing supplier type code
    // value.project_code = value.project_code.toUpperCase();
    updateProject(id, value);
  };

  handleSubmitHistory = value => {
    const { updateProjectHistory } = this.props;
    const { id } = this.props.match.params;
    value.id_project = id;
    updateProjectHistory(value);
  };

  render() {
    // console.log(this.props.project);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="projects.update.title" />}
            style={{ margin: true }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="projects.tab.information" />}
              key="1"
            >
              {this.props.project.loading &&
                !this.props.project.loadingSubmit ? (
                  <div className="spinStyle">
                    <Spin size="large" />
                  </div>
                ) : (
                  <ProjectUpdateForm
                    submit={this.handleSubmit}
                    project={this.props.project}
                  />
                )}
            </TabPane>
            <TabPane tab={<IntlMessages id="projects.tab.history" />}
              key="2"
            >
              <ProjectHistoryUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmitHistory}
                project={this.props.project}
              />
            </TabPane>
          </Tabs>

          {/* {this.props.project.loading &&
          !this.props.project.loadingSubmit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <ProjectUpdateForm
              submit={this.handleSubmit}
              project={this.props.project}
            />
          )} */}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, project: state.project };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateProject);
