import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, DatePicker, Row, Col, Alert } from "antd";
import moment from "moment";

const { Option } = Select;
const { TextArea } = Input;
// const InputGroup = Input.Group;
const { RangePicker } = DatePicker;


class ProjectUpdateForm extends Component {

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log("やった")
        values.start_date = values.date[0].format("YYYY-MM-DD");
        values.end_date   = values.date[1].format("YYYY-MM-DD");
        delete values['date'];
        // console.log(values);
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { project } = this.props;
    const { loading, loadingSubmit } = project;
    const data = project.project ? project.project : [];

    const used = data.is_used;
    const error = project.errorField ? project.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="projects.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
          ""
        )}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="projects.code" />}
              validateStatus={
                error.project_code || getFieldError("project_code")
                  ? "error"
                  : null
              }
              help={
                error.project_code
                  ? error.project_code
                  : getFieldError("project_code")
              }
            >
              {getFieldDecorator("project_code", {
                initialValue: data.project_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(
                <Input
                  disabled={used ? true : false}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="projects.name" />}
            >
              {getFieldDecorator("project_name", {
                initialValue: data.project_name,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
                })
                (<Input 
                  //autosize={{ minRows: 2 }} 
                    disabled={used ? true : false} 
                  />
                )
              }
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="projects.parent" />}
            >
              {getFieldDecorator("id_project_parent", {
                initialValue: data.id_project_parent,
              })(
                <Select
                  loading={this.props.project.loading}
                  showSearch
                  optionFilterProp="children"
                  allowClear={true}
                  disabled={used ? true : false}
                >
                  {this.props.project.project_parents.map((x, key) => (
                    data.id_project !== x.id_project
                    ?
                      (
                        <Option key={key} value={x.id_project}>
                          {x.project_code + " ("+ x.project_name +")"}
                        </Option>
                      )
                    : null
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="rangeDate" />}
            >
              {getFieldDecorator("date", {
                initialValue: 
                  data.start_date &&
                  data.end_date
                    ? [
                        moment(data.start_date),
                        moment(data.end_date)
                      ]
                    : [],
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <RangePicker
                  format="DD MMMM YYYY"
                  style={{ width: "100%" }}
                />
              )}
            </Form.Item>
          
          </div>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>

          <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="projects.status" />}
            >
              {getFieldDecorator("status", {
                initialValue: data.status,
              })(
                <Select
                  showSearch
                  optionFilterProp="children"
                >
                  <Option key={1} value={1}>New</Option>
                  <Option key={2} value={2}>Estimate</Option>
                  <Option key={3} value={3}>Approved</Option>
                  <Option key={4} value={4}>Started</Option>
                  <Option key={5} value={5}>Finished</Option>
                  <Option key={6} value={6}>Cancelled</Option>
                  <Option key={7} value={7}>On Hold</Option>
                </Select>
              )}
            </Form.Item>
            
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="projects.manager" />}
            >
              {getFieldDecorator("id_employee_manager", {
                initialValue: data.id_employee_manager,
              })(
                <Select
                  loading={this.props.project.loading}
                  showSearch
                  optionFilterProp="children"
                  allowClear={true}
                  //disabled={used ? true : false}
                >
                  {this.props.project.employees.map((data, key) => (
                    <Option key={key} value={data.id_employee}>
                      {data.employee_code + " (" + data.first_name + (data.last_name ?  " " + data.last_name :"")  + ")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="projects.customer" />}
            >
              {getFieldDecorator("id_customer", {
                initialValue: data.id_customer,
              })(
                <Select
                  loading={this.props.project.loading}
                  showSearch
                  optionFilterProp="children"
                  allowClear={true}
                  //disabled={used ? true : false}
                >
                  {this.props.project.customers.map((data, key) => (
                    <Option key={key} value={data.id_customer}>
                      {data.customer_code +" ("+data.customer_name+")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="locations.add.description" />}
            >
              {getFieldDecorator("description", {
                initialValue: data.description
                // rules: [
                //   {
                //     required: true,
                //     message: <IntlMessages id="required" />
                //   }
                // ]
                })
                (
                  <TextArea 
                    autosize={{ minRows: 2 }} 
                    //disabled={used ? true : false} 
                    />
                )}
            </Form.Item>
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading || loadingSubmit}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    ); 
  }
}

const WrappedProjectUpdateForm = Form.create({
  name: "update_project"
})(ProjectUpdateForm);

export default WrappedProjectUpdateForm;
