import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, DatePicker, InputNumber, Col, Divider, Timeline, Progress } from "antd";
import moment from "moment";

// const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;
// const { RangePicker } = DatePicker;


class ProjectHistoryUpdateForm extends Component {

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log("やった")
        values.project_history_progress = values.project_history_progress/100;
        values.project_history_date     = values.project_history_date.format("YYYY-MM-DD");

        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { project } = this.props;
    const { loading, loadingSubmit } = project;
    const histories = project.project ? project.project.progress : [];

    // const used = data.is_used;
    // const error = project.errorField ? project.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <InputGroup compact>
              <Col span={6}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="progress" />}
                >
                  {getFieldDecorator("project_history_progress", {
                    initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                    ]
                  })(
                    <InputNumber
                      //disabled={used ? true : false}
                      min={0}
                      max={100}
                      formatter={value => `${value}%`}
                      parser={value => value.replace('%', '')}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={18}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="date" />}
                >
                  {getFieldDecorator("project_history_date", {
                    initialValue: moment(new Date(), "YYYY-MM-DD"),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      },
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      allowClear={false}
                      // showTime={{ format: "HH:mm:ss" }}
                      format="DD MMMM YYYY"
                      disabledDate={e => e.valueOf() > moment()}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="customers.add.notes" />}
            >
              {getFieldDecorator("project_history_notes", {
                // initialValue: data.notes
              })(<TextArea autosize={{ minRows: 2 }} />)}
            </Form.Item>

            <Form.Item {...tailFormItemLayout}>
              <Button
                type="primary"
                htmlType="submit"
                style={{ marginTop: "20px" }}
                loading={loading || loadingSubmit}
              >
                <IntlMessages id="button.submit" />
              </Button>
            </Form.Item>
          
            
          </div>
          <Divider type="vertical" style={{ height: "40vh" }} />
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 0 0 15px" }}>
            
            <Progress 
              percent={histories[0].project_history_progress * 100} 
              style={{ marginBottom:"20px" }}  
            />

            <Timeline>
              { 
                histories.map((data) => {
                  return <Timeline.Item>{
                   (data.project_history_notes ? data.project_history_notes + " - " : "") +
                   moment(data.project_history_date).format('DD/MM/YYYY') + " - " +
                   `${data.project_history_progress*100}%` + 
                   " - updated by " + data.created_by 
                  }</Timeline.Item>
                })
              }    
            </Timeline>
            
          </div>
        </div>
      </Form>
    ); 
  }
}

const WrappedProjectHistoryUpdateForm = Form.create({
  name: "update_project_history"
})(ProjectHistoryUpdateForm);

export default WrappedProjectHistoryUpdateForm;
